                <p>8.1.5. Az gyfelet azonban nem illeti meg az a jog, hogy ugyanazon hiba miatt kellkszavatossgi s jtllsi ignyt, illetve termkszavatossgi s jtllsi ignyt egyszerre, egymssal prhuzamosan rvnyestsen. Ezen korltozsoktl fggetlenl az gyfelet a jtllsbl fakad jogok a 9.1 s a 9.2 pontokban meghatrozott jogosultsgoktl fggetlenl megilletik.</p>
